import React, { memo, useState, useEffect } from "react";
import { testQuestion } from "@/common/local-data.js";
import TestQuestion from "./TestQuestion";
import { Button } from "antd";
import { shallowEqual, useDispatch, useSelector } from "react-redux";
import {
  changeShowAction,
  getUserNewGradesAction,
} from "./store/actioncreators";
import dateFormat from "@/util/date-format";

import {
  TestWrapper,
  TestFooter,
  TestHeader,
  TestJudger,
  TestResult,
  GradesRank,
  MyGrades,
} from "./style";
import axios from "axios";

export default memo(function Test() {
  const totalIndex = testQuestion.length;

  //当前题目下标
  const [currentIndex, setCurrentIndex] = useState(1);

  //最终得分
  const [grades, setGrades] = useState(100);

  const [judegerShow, setJudegerShow] = useState(false);

  const [resultShow, setResultShow] = useState(false);

  const [isSubmit, setIsSubmit] = useState(false); //指示用户是否已经提交过测试，显示答案

  //指示错题下标
  const [error, setError] = useState([]);
  const [right, setRight] = useState([]);

  const dispatch = useDispatch();

  const { answers, gradesList, rank, timeList } = useSelector(
    (state) => ({
      answers: state.answers,
      gradesList: state.gradesList,
      rank: state.rank,
      timeList: state.timeList,
    }),
    shallowEqual
  );

  //页面挂载时也进行网络请求，获取用户的成绩单
  useEffect(() => {
    dispatch(getUserNewGradesAction(sessionStorage.getItem("username")));
  }, [dispatch]);

  function lastQuestion() {
    if (currentIndex === 1) {
      setCurrentIndex(10);
    } else {
      setCurrentIndex(currentIndex - 1);
    }
  }

  function nextQuestion() {
    if (currentIndex === 10) {
      setCurrentIndex(1);
    } else {
      setCurrentIndex(currentIndex + 1);
    }
  }

  //交卷弹窗
  function checkResult() {
    let isAllDone = true;
    for (let i = 0; i < 10; i++) {
      if (answers[i] == null) {
        dispatch(changeShowAction(true)); //显示遮罩层
        setJudegerShow(true); //显示提示框
        isAllDone = false;
        break;
      }
    }
    //如果所有题全部答完
    if (isAllDone) {
      dispatch(changeShowAction(true));
      setResultShow(true);
      gradesCounter();
    }
  }

  //分数判断
  function gradesCounter() {
    for (let i = 0; i < 10; i++) {
      if (answers[i] == null || answers[i].length === 0) {
        setError((state) => {
          const newError = [...state];
          newError.push(i);
          return newError;
        });
        //setState合并 要用函数
        setGrades((state) => {
          return state - 10;
        });
      } else {
        for (let j = 0; j < answers[i].length; j++) {
          if (
            !testQuestion[i].answer.includes(answers[i][j]) ||
            answers[i].length < testQuestion[i].answer.length
          ) {
            setError((state) => {
              const newError = [...state];
              newError.push(i);
              return newError;
            });
            setGrades((state) => {
              return state - 10;
            });
            break;
          } else {
            setRight((state) => {
              const newRight = [...state];
              newRight.push(i);
              return newRight;
            });
          }
        }
      }
    }

    setIsSubmit(true);
    //将成绩上传到服务器
    gradesSubmit(grades);
    //获取用户最新成绩提交列表
    dispatch(getUserNewGradesAction(sessionStorage.getItem("username")));
    //获取最新成绩榜单
  }

  //将成绩提交给服务器
  function gradesSubmit(value) {
    const username = sessionStorage.getItem("username");
    const time = dateFormat("mm-dd HH:MM", new Date());
    axios({
      method: "get",
      url: "/updateReport",
      params: {
        score: value,
        username,
        time,
      },
    });
  }

  return (
    <TestWrapper>
      <TestHeader>
        <h1>学四史专项测试</h1>
        <div className="test-title">
          <span className="index">{`${currentIndex}/${totalIndex}`}</span>
          <span className="type">
            {testQuestion[currentIndex - 1].answer.length === 1
              ? "[单选题]"
              : "[不定项选择题]"}
          </span>
        </div>
      </TestHeader>
      <TestQuestion
        info={testQuestion[currentIndex - 1]}
        index={currentIndex}
        isSubmit={isSubmit}
      ></TestQuestion>
      <TestFooter>
        <div className="control">
          <Button
            type="primary"
            danger
            className="btn submit"
            onClick={() => {
              checkResult();
            }}
          >
            交卷
          </Button>

          <Button type="primary" className="btn next" onClick={nextQuestion}>
            下一题
          </Button>
          <Button type="primary" className="btn prev" onClick={lastQuestion}>
            上一题
          </Button>
        </div>
        <div className="sheet">
          <div className="sheet-title">
            <h2>答题卡</h2>
          </div>
          <ul>
            {testQuestion.map((item, index) => {
              return (
                <li
                  className={`${index + 1 === currentIndex ? "active" : ""} ${
                    error.includes(index) ? "wrong" : ""
                  } ${right.includes(index) ? "right" : ""}`}
                  key={item.question}
                  onClick={() => {
                    setCurrentIndex(index + 1);
                  }}
                >
                  {index + 1}
                </li>
              );
            })}
          </ul>
        </div>
        <div className="info">
          <span>单选择题9道 | 不定项选择题1道</span>
        </div>
      </TestFooter>
      <TestJudger className={judegerShow ? "show" : "hidden"}>
        <div className="alert">
          <div className="alert-title">
            <span></span>
            <h2>交卷前提示</h2>
          </div>
          <div className="alert-content">
            <div className="img"></div>
            <p>你还有部分题目未完成，确定交卷？</p>
            <p>交卷后可查看所有答案</p>
          </div>
          <div className="alert-footer">
            <a
              href="##"
              className="continue"
              onClick={(e) => {
                e.preventDefault();
                dispatch(changeShowAction(false));
                setJudegerShow(false);
              }}
            >
              继续做题
            </a>
            <a
              href="##"
              className="submit"
              onClick={(e) => {
                e.preventDefault();
                setJudegerShow(false);
                setResultShow(true);
                gradesCounter();
              }}
            >
              立即交卷
            </a>
          </div>
        </div>
      </TestJudger>
      <TestResult className={resultShow ? "show" : "hidden"}>
        <div className="result-title">
          <span></span>
          <h2>测试结果</h2>
        </div>
        <div className="result-content">
          <p>您的最终得分为</p>
          <h1>{grades}</h1>
        </div>
        <div className="result-footer">
          <a
            href="##"
            className="continue"
            onClick={(e) => {
              e.preventDefault();
              setResultShow(false); //关闭提示框
              dispatch(changeShowAction(false)); //关闭遮罩层
              setGrades(100); //重置成绩
            }}
          >
            继续
          </a>
        </div>
      </TestResult>
      <GradesRank>
        <div className="rank-header">
          <span></span>
          <h2>成绩排行榜</h2>
        </div>
        <div className="rank-content">
          <div className="first">
            <span className="top1">1</span>
            <h3 className="user">LSW</h3>
            <div className="grades">100</div>
          </div>
          <div className="second">
            <span className="top2">2</span>
            <h3 className="user">LSW</h3>
            <div className="grades">90</div>
          </div>
          <div className="third">
            <span className="top3">3</span>
            <h3 className="user">LSW</h3>
            <div className="grades">80</div>
          </div>
        </div>
      </GradesRank>
      <MyGrades>
        <div className="my-header">
          <span></span>
          <h2>我的最近提交</h2>
        </div>
        <div className="my-content">
          <div className="new one">
            <div className="grades">成绩：{gradesList[0]}</div>
            <div className="time">{timeList[0]}</div>
          </div>

          <div className="new tow">
            <div className="grades">成绩：{gradesList[1]}</div>
            <div className="time">{timeList[1]}</div>
          </div>
          <div className="new three">
            <div className="grades">成绩：{gradesList[2]}</div>
            <div className="time">{timeList[2]}</div>
          </div>
        </div>
      </MyGrades>
    </TestWrapper>
  );
});
