import * as actionTypes from "./constants";
import { getUserNewGrades, getNewGradesRank } from "@/services/grades.js";

const changeSelectedAnswer = (selectedAnswer) => {
  return {
    type: actionTypes.SELECT_ANSWER,
    selectedAnswer,
  };
};

export const changeSelectedAnswerAction = (tag, value) => {
  return (dispatch, getState) => {
    const currentSelectedAnswer = getState().answers;
    const newSelectedAnswer = { ...currentSelectedAnswer };
    newSelectedAnswer[tag - 1] = value;
    dispatch(changeSelectedAnswer(newSelectedAnswer));
  };
};

export const changeShowAction = (isShow) => {
  return {
    type: actionTypes.CHANGE_SHOW,
    isShow,
  };
};

export const changeLoginAction = (isLogin) => {
  return {
    type: actionTypes.LOGIN,
    isLogin,
  };
};

export const changeUsernameAction = (username) => {
  return {
    type: actionTypes.CHANGE_USERNAME,
    username,
  };
};

const changeUserNewGradesAction = (grades) => {
  return {
    type: actionTypes.CHANGE_USERNAME,
    grades,
  };
};

const changeNewSubmitTimeAction = (times) => {
  return {
    type: actionTypes.CHANGE_SUBMIT_TIME,
    times,
  };
};

//获取用户最新成绩单
export const getUserNewGradesAction = (username) => {
  return (dispatch, getState) => {
    getUserNewGrades(username).then((res) => {
      dispatch(changeUserNewGradesAction(res.data.scores));
      dispatch(changeNewSubmitTimeAction(res.data.times));
    });
  };
};

const changeNewGradesRank = (rank) => {
  return {
    type: actionTypes.CHANGE_GRADES_RANK,
    rank,
  };
};

//获取最新成绩榜单
export const getNewGradesRankAction = () => {
  return (dispatch) => {
    getNewGradesRank().then((res) => {
      dispatch(changeNewGradesRank(res.data.rank));
    });
  };
};
