import styled from "styled-components";
import pic from "@/assets/img/13.png";
import top1 from "@/assets/img/ico1.png";
import top2 from "@/assets/img/ico2.png";
import top3 from "@/assets/img/ico3.png";

export const TestWrapper = styled.div`
  position: relative;
  width: 1000px;
  /* height: 500px; */
  margin: 0 auto;
  margin-top: 10px;
  margin-bottom: 20px;
  border: 1px solid #ccc;
  background-color: #fff;
`;

export const TestHeader = styled.div`
  width: 100%;
  height: 150px;
  line-height: 50px;

  h1 {
    margin: 0;
    padding-top: 20px;
    padding-left: 30px;
    padding-bottom: 20px;
    border-bottom: 1px solid #eee;
  }

  .test-title {
    height: 40px;

    span {
      //display: inline-block;
    }

    .index {
      margin-left: 20px;
      padding: 0 5px;
      border-radius: 5px;
      font-size: 18px;
      background-color: #e5e5e5;
    }

    .type {
      margin-left: 20px;
      font-size: 18px;
    }
  }
`;

export const TestFooter = styled.div`
  position: relative;
  .control {
    margin-top: 50px;

    .btn {
      width: 110px;
      height: 40px;
    }

    .submit {
      margin-left: 20px;
      margin-bottom: 20px;
    }

    .next,
    .prev {
      float: right;
      margin-right: 30px;
    }
  }

  .sheet {
    border-top: 1px dashed #ddd;
    height: 200px;
    padding: 20px;

    ul {
      margin-top: 50px;
      li {
        float: left;
        margin-right: 10px;
        width: 50px;
        height: 50px;
        line-height: 50px;
        text-align: center;
        color: #dce4ec;
        font-size: 18px;
        font-weight: bold;
        cursor: pointer;
      }

      .active,
      li:hover {
        color: #fff;
        background-color: #1890ff;
      }
    }
  }

  .info {
    position: absolute;
    right: 40px;
    top: 85px;
    background-color: #f2f2f2;
    span {
      display: inline-block;
      padding: 5px;
      font-size: 16px;
    }
  }
`;

export const TestJudger = styled.div`
  position: absolute;
  top: 169px;
  left: 165px;
  width: 630px;
  z-index: 9999;

  .alert-title {
    position: relative;
    span {
      float: left;
      margin-top: 20px;
      margin-left: 25px;
      width: 8px;
      height: 8px;
      background-color: orange;
    }

    h2 {
      margin: 0;
      float: left;
      margin-top: 13px;
      margin-left: 9px;
      line-height: 18px;
      font-size: 18px;
      font-weight: 400;
    }
    width: 100%;
    height: 50px;
    background: #f0f0f0;
    border-radius: 4px 4px 0 0;
  }

  .alert-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 240px;
    background-color: #fff;

    .img {
      width: 80px;
      height: 80px;
      background: url(${pic});
      margin-bottom: 10px;
    }

    p {
      font-size: 16px;
    }
  }

  .alert-footer {
    width: 100%;
    height: 70px;
    background: #f0f0f0;
    border-radius: 4px 4px 0 0;

    a {
      float: right;
      line-height: 40px;
      color: white;
      font-size: 16px;
      text-align: center;
      width: 110px;
      height: 40px;
      margin-right: 10px;
      margin-top: 15px;
      border-radius: 4px;
    }

    .continue {
      background-color: #bbb;
    }

    .submit {
      background-color: #25bb9b;
    }
  }
`;

