export const GradesRank = styled.div`
  position: absolute;
  width: 300px;
  height: 389px;
  top: 50px;
  right: -325px;
  background-color: #fff;
  border: 1px solid #ccc;

  .rank-header {
    padding-top: 10px;

    font-size: 16px;

    span {
      display: inline-block;
      height: 10px;
      width: 10px;
      margin-left: 10px;
      background-color: #ff6547;
    }

    h2 {
      padding-left: 15px;
      font-weight: bolder;
      display: inline-block;
    }
  }

  .rank-content {
    span {
      display: inline-block;
      font-size: 14px;
      text-align: center;
      line-height: 20px;
      width: 22px;
      height: 30px;
      color: #fff;
    }

    .top1 {
      background: url(${top1}) no-repeat;
    }

    .top2 {
      background: url(${top2}) no-repeat;
    }

    .top3 {
      background: url(${top3}) no-repeat;
    }

    h3 {
      display: inline-block;
      font-size: 18px;
      font-weight: bold;
      margin-left: 70px;
    }

    .first,
    .second,
    .third {
      padding: 20px 20px;
      border-bottom: 1px dashed #ccc;
    }

    .first {
      .user {
        color: red;
      }
    }

    .second {
      .user {
        color: yellowgreen;
      }
    }

    .third {
      .user {
        color: #25bb9b;
      }
      border-bottom: 0;
    }

    .grades {
      font-size: 20px;
      font-weight: bolder;
      margin-left: 95px;
    }
  }
`;

export const MyGrades = styled.div`
  position: absolute;
  width: 300px;
  height: 300px;
  top: 450px;
  right: -325px;
  background-color: #fff;
  border: 1px solid #ccc;

  .my-header {
    padding-top: 10px;

    font-size: 16px;

    span {
      display: inline-block;
      height: 10px;
      width: 10px;
      margin-left: 10px;
      background-color: #ff6547;
    }

    h2 {
      padding-left: 15px;
      font-weight: bolder;
      display: inline-block;
    }
  }

  .my-content {
    .new {
      padding: 14px 20px;
      margin-left: 60px;
      .grades {
        font-size: 18px;
        font-weight: bold;
      }
    }
  }
`;
