import axios from "axios";

//获取个人成绩提交列表
export const getUserNewGrades = (username) => {
  return axios({
    method: "get",
    url: "/checkReport",
    params: {
      username,
    },
  });
};

//获取成绩榜单
export const getNewGradesRank = () => {
  return axios({
    method: "get",
    url: "/rank",
  });
};
