import * as actionTypes from "./constants";

const defaultState = {
  answers: {},
  isShow: false,
  isLogin: true,
  username: "",
  gradesList: [null, null, null],
  timeList: [null, null, null],
  rank: [null, null, null],
};

export default function reducer(state = defaultState, action) {
  switch (action.type) {
    case actionTypes.SELECT_ANSWER:
      return { ...state, answers: action.selectedAnswer };
    case actionTypes.CHANGE_SHOW:
      return { ...state, isShow: action.isShow };
    case actionTypes.LOGIN:
      return { ...state, isLogin: action.isLogin };
    case actionTypes.CHANGE_USERNAME:
      return { ...state, username: action.username };
    case actionTypes.CHANGE_USER_GRADES:
      return { ...state, gradesList: action.grades };
    case actionTypes.CHANGE_SUBMIT_TIME:
      return { ...state, gradesList: action.times };
    case actionTypes.CHANGE_GRADES_RANK:
      return { ...state, rank: action.rank };
    default:
      return state;
  }
}
