import styled from "styled-components";
import left from "@/assets/img/banner-control-left.png";
import right from "@/assets/img/banner-control-right.png";
import war1 from "@/assets/img/war.JPG";
import war2 from "@/assets/img/war2.JPG";
import war3 from "@/assets/img/war3.JPG";
import meeting4 from "@/assets/img/meeting4.JPG";
import war5 from "@/assets/img/war5.JPG";
import gaokao1 from "@/assets/img/gaokao.JPG";
import gaokao2 from "@/assets/img/gaokao2.JPG";
export const HomeWrapper = styled.div`
  background-color: #fff;
  .banner {
    margin: 0 auto;
    width: 1400px;
    position: relative;
    img {
      width: 1400px;
      height: 600px;
    }
    .pre {
      position: absolute;
      left: 5px;
      top: 250px;
      width: 37px;
      height: 67px;
      cursor: pointer;
      background: url(${left});
    }
    .next {
      position: absolute;
      right: 5px;
      top: 250px;
      width: 37px;
      cursor: pointer;
      height: 67px;
      background: url(${right});
    }
    .next:hover,
    .pre:hover {
      background-color: black;
      opacity: 0.5;
    }
  }

  .content {
    margin: 0 auto;
    width: 1400px;
    background-color: #c20c0c;
    border-left: 1px solid #ccc;
    border-right: 1px solid #ccc;

    .dang-history {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      .pic-title {
        height: 60px;
        line-height: 60px;
        font-size: 20px;
        color: #fff;
        margin: 0 auto;
      }

      .title {
        position: relative;

        h2 {
          position: absolute;
          top: 100px;
          left: 40px;
          font-size: 40px;
          color: white;
          font-weight: bolder;
        }

        .title-img {
          width: 1400px;
        }

        p {
          position: absolute;
          bottom: 40px;
          right: 20px;
          font-size: 25px;
          color: white;
          border-bottom: 1px solid #fff;
        }
      }

      .build-history {
        .build-title {
          display: flex;
          justify-content: center;
          align-items: center;
          flex-direction: column;
          width: 300px;
          margin: 0 auto;
          margin-top: 50px;
          h2 {
            font-size: 20px;
            color: #fff;
          }
          p {
            color: #fff;
            font-size: 18px;
          }

          .decoration {
            width: 327px;
            span {
              display: inline-block;
            }
            .left,
            .right {
              width: 150px;
              height: 2px;
              background-color: #fff;
            }
            .center {
              margin: 0 10px 0 10px;
              width: 7px;
              height: 7px;
              background-color: #fff;
            }
          }
        }

        .build-content {
          margin-top: 40px;

          p {
            margin-left: 50px;
            font-size: 16px;
            color: #fff;
          }

          .meeting-banner {
            margin-top: 20px;
            width: 800px;
            height: 450px;
            h2 {
              color: #fff;
              margin-left: 360px;
              margin-bottom: 10px;
            }
            img {
              width: 800px;
              height: 400px;
            }
          }
        }
      }
    }

    .war-history {
      margin-top: 30px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;

      .war-title {
        width: 400px;
        h2 {
          text-align: center;
          font-size: 20px;
          color: #fff;
        }
        p {
          text-align: center;
          color: #fff;
          font-size: 18px;
        }

        .decoration {
          width: 327px;
          margin-left: 45px;
          span {
            display: inline-block;
          }
          .left,
          .right {
            width: 150px;
            height: 2px;
            background-color: #fff;
          }
          .center {
            margin: 0 10px 0 10px;
            width: 7px;
            height: 7px;
            background-color: #fff;
          }
        }
      }

      .war-content {
        display: flex;
        width: 100%;
        height: 500px;
        margin-top: 20px;
        .war-left {
          flex: 2;
          display: flex;
          .war-img-1 {
            flex: 1;
            margin: 5px;
            background: url(${war1});
            background-size: cover;
          }
          .right-img {
            display: flex;
            flex-direction: column;
            flex: 1;
            .war-img-2 {
              margin: 5px;
              flex: 1;
              background: url(${war2});
              background-size: cover;
            }
            .war-img-3 {
              margin: 5px;
              flex: 1;
              background: url(${war3});
              background-size: cover;
            }
          }
        }
        .war-right {
          flex: 1;
          p {
            font-size: 18px;
            margin: 20px;
            color: #fff;
          }
        }
      }
    }

    .socialist-history {
      margin-top: 30px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      .socialist-title {
        width: 400px;
        h2 {
          text-align: center;
          font-size: 20px;
          color: #fff;
        }
        p {
          text-align: center;
          color: #fff;
          font-size: 18px;
        }

        .decoration {
          width: 327px;
          margin-left: 45px;
          span {
            display: inline-block;
          }
          .left,
          .right {
            width: 150px;
            height: 2px;
            background-color: #fff;
          }
          .center {
            margin: 0 10px 0 10px;
            width: 7px;
            height: 7px;
            background-color: #fff;
          }
        }
      }

      .socialist-content {
        margin-top: 40px;
        padding-left: 10px;
        display: flex;
        height: 500px;
        width: 1400px;

        .so-left {
          flex: 1;
          .so-banner {
            width: 600px;
            img {
              width: 600px;
            }
          }

          .so-banner-info {
            margin-top: 15px;
            font-size: 20px;
            color: #fff;
            text-align: center;
            font-weight: bolder;
          }
        }
        .so-right {
          flex: 3;
          display: flex;
          flex-direction: column;
          div {
            flex: 1;
          }
          p {
            position: relative;
            padding: 10px;
            overflow: hidden;
            top: 171px;
            height: 60px;
            color: #fff;
            background-color: black;
            background-color: rgba(5, 5, 5, 0.59);
          }
          .so-top {
            flex: 1;
            display: flex;
          }
          .so-bottom {
            flex: 1;
            display: flex;
          }
          .army {
            margin: 10px;
            background: url(${war5});
            background-size: cover;
          }
          .meeting {
            margin: 10px;
            margin-top: 0;
            background: url(${meeting4});
            background-size: cover;
          }
          .shijian {
            margin: 10px;
            background: url(${gaokao2});
            background-size: cover;
          }
          .gaokao {
            margin: 10px;
            margin-top: 0;
            background: url(${gaokao1});
            background-size: cover;
          }
        }
      }
    }
  }
`;
